import { Component } from '@angular/core';
import { NgModule } from '@angular/core';

declare var window: any;

interface Song {
  name: string,
  artist: string,
  url: string,
}

@Component({
  selector: 'app-tab4',
  templateUrl: 'tab4.page.html',
  styleUrls: ['tab4.page.scss']
})
export class Tab4Page {

  songs: Song[]

  constructor() {
    this.songs = []
  }

  ngOnInit() {
    fetch("https://ws.audioscrobbler.com/2.0/?method=chart.gettoptracks&api_key=62e6801a4e0e747b632aef27e53e8c01&format=json", {
      "method": "GET",
    })
      .then(response => {
        return response.json()
      })
      .then(data => {
        console.log(data)
        data.tracks.track.forEach(song => {
          let songItem = {
            name: song.name,
            artist: song.artist.name,
            url: song.url
          }
          this.songs.push(songItem)
        });
      })
  }

  openSong(song) {
    window.cordova.InAppBrowser.open(song, '_blank')
  }

}
